.signup-container {
  form {
    // position: fixed;
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    width: 50%;
    background-color: rgb(201, 201, 201);
    color: rgb(1, 7, 17); 
    border-radius: 10px;
    padding: 20px;
    z-index: 1;
    > * {
      margin-block-end: 20px;
    }
    input {
      flex: 1;
      height: 35px;
      border: 1px solid grey;
      margin-inline-end: 10px;
      background-color: whitesmoke;
    }
  }
  .signup-img {
    width: 300px;
    position: absolute;
    bottom: 20px;
    right: 20px;
  }
}
