.contact-preview {
  background-color: rgb(201, 201, 201);
  width: 100%;
  border-radius: 20px;
  margin-block-end: 10px;
  padding: 10px;
  cursor: pointer;
  &:hover {
    background-color: rgb(160, 159, 159);
  }
  img {
    width: 100px;
    border-radius: 50%;
  }
  p {
    margin-inline-start: 20px;
  }
}
