.home-page {
  background-image: url(../../assets/imgs/home-1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  .home-page-content {
    width: 50%;
    margin: 0 0 0 auto;
    .hello-container {
      background-color: #c2c2c2;
      color: rgb(1, 7, 17);
      text-align: left;
      width: 100%;
      margin: auto;
      padding: 20px;
      border-radius: 10px;
      .coins-icon{
          width: 20px;
      }
      span {
        margin-inline-start: 10px;
      }
    }
    .btn-cta {
      width: 100%;
      margin-top: 20px;
    }
  }
}
